const gradient = (scalarField: ScalarField) => ([x, y]: Vector2): Vector2 => {
	const d = 0.01;
	const left = scalarField([x - d, y]);
	const right = scalarField([x + d, y]);
	const down = scalarField([x, y - d]);
	const up = scalarField([x, y + d]);
	return Vec2.multiply(
		[
			right - left,
			up - down
		],
		1 / (2 * d)
	);
};


function applyWindowSizeToCanvas(canvas: HTMLCanvasElement){
	const widthPx = window.innerWidth;
	const heightPx = window.innerHeight;
	const scalePx = window.devicePixelRatio || 1;
	Object.assign(canvas.style, {
		width: `${widthPx}px`,
		height: `${heightPx}px`
	});
	Object.assign(canvas, {
		width: widthPx * scalePx,
		height: heightPx * scalePx
	});
}

function autoAdjustCanvasSize(canvas: HTMLCanvasElement, onResize: Function){
	window.onresize = () => {
		applyWindowSizeToCanvas(canvas);
		onResize();
	};
}

type Morphism<A, B> = (a: A) => B;

type OrthoOrbitCamera = OrbitCamera & { orthoSize: Vector2 };
type OrbitCamTransformation = (cam: OrthoOrbitCamera) => OrthoOrbitCamera;
function setupOrbitCameraControl(canvas: HTMLCanvasElement, transformCamera: Morphism<OrbitCamTransformation, void>) {
	canvas.addEventListener("mousemove", e => {
		if (e.buttons !== 1) return;
		const s = 0.01;
		transformCamera(
			cam => ({
				...cam,
				longitude: cam.longitude + e.movementX * s,
				latitude: cam.latitude + e.movementY * s
			})
		);
	});
	canvas.addEventListener("wheel", e => {
		transformCamera(
			cam => ({
				...cam,
				radius: cam.radius * (1 + e.deltaY * 0.001)
			})
		);
	});
}


//cube ###

type Cube = {
	center: Vector3,
	size: Vector3,
	color: Vector3
};
function getNonZeroIndex(v: Vector3): number {
	if (v[0] !== 0) return 0;
	if (v[1] !== 0) return 1;
	if (v[2] !== 0) return 2;
}
const renderCube = (ctx: CanvasRenderingContext2D, camera: PerspectiveCamera) => (cube: Cube) => {
	for (const normal of voxelFaceNormals) {
		const orthoAxes = getOrthogonalAxes(normal);
		const orthoAxesInds = orthoAxes.map(getNonZeroIndex) as [number, number];
		const localFaceSize = orthoAxesInds.map(i => cube.size[i]) as Vector2;
		const localFaceVertices: Vector2[] = [
			[+localFaceSize[0], +localFaceSize[1]],
			[-localFaceSize[0], +localFaceSize[1]],
			[-localFaceSize[0], -localFaceSize[1]],
			[+localFaceSize[0], -localFaceSize[1]],
		];
		const faceCenter = Vec3.add(cube.center, Vec3.multiply(normal, cube.size[getNonZeroIndex(normal)]));
		const curProjectionOpt = projectVoxelFace(ctx, camera, localFaceVertices, faceCenter, normal);
		if (isNone(curProjectionOpt)) continue;

		const adjustedColor = cube.color.map(Math.round) as Vector3;
		ctx.fillStyle = vec3ToColor(adjustedColor);
		pathPolygon(ctx, curProjectionOpt.value);
		ctx.fill();
		ctx.stroke();
	}
};


//quad/triangle-pair ###

const localCorners: Vector2[] = [
	[+1, +1],
	[-1, +1],
	[+1, -1],
	[-1, -1]
];
const triangles: [number, number, number][] = [
	[0, 1, 3], [3, 2, 0]
];
function renderHeightmapQuad(
	ctx: CanvasRenderingContext2D, cam: PerspectiveCamera, quadSize: number, scalarField: ScalarField,
	point: Vector2, color: Vector3) {
	
	const globalCorners = map(
		(localCorner: Vector2) => {
			const corner2d = Vec2.add(point, Vec2.multiply(localCorner, quadSize));
			const y = scalarField(corner2d);
			return [corner2d[0], y, corner2d[1]] as Vector3;
		}
	)(localCorners);
	const verts = map(worldPointToScreenPoint(ctx, cam))(globalCorners);
	ctx.fillStyle = vec3ToColor(color);
	for (const tri of triangles) {
		const triVerts = tri.map(i => verts[i]);
		pathPolygon(ctx, triVerts);
		ctx.fill();
		pathPolyline(ctx, triVerts);
		ctx.stroke();
	}
}

type Shape = "triangle" | "cube" | "sphere";

function renderHeightMap(
	ctx: CanvasRenderingContext2D, perspectiveCam: PerspectiveCamera, 
	negativeColor: Vector3, positiveColor: Vector3, shape: Shape,
	gridField: GridField, scalarField: ScalarField) {
	
	const worldToCam = worldPointToCamPoint(perspectiveCam);
	const camToScreen = camPointToScreenPoint(ctx, perspectiveCam);
	const sortedPoints = calculateScalarFieldOrder(perspectiveCam.transform.position, gridField);
	const maxPoint = gridToWorldCoordinate(gridField)([gridField.size[0], gridField.size[1]]);

	const cellSize = gridField.cellSize / 2;

	for (const point2d of sortedPoints) {
		const y = scalarField(point2d);
		const color = Vec3.interpolate(negativeColor, positiveColor, normalize(-1, +1, y));

		if (shape === "cube"){
			const cubeY = y;
			renderCube(ctx, perspectiveCam)({ 
				center: [point2d[0], cubeY / 2, point2d[1]],
				size: [cellSize, Math.abs(cubeY / 2), cellSize],
				color, 
			});
		}
		else if (shape === "sphere"){
			const point = [point2d[0], y, point2d[1]] as Vector3;
			const camPosition = worldToCam(point);
			const dist = Vec3.magnitude(camPosition);
			const screenPosition = camToScreen(camPosition);
			drawDisc(ctx, screenPosition, 40 / dist, { fillStyle: vec3ToColor(color) });
		}
		else if (shape === "triangle"){
			//without the offset, the center of the quad would be exactly on the center of a grid point.
			//but we actually want the corners of the quad to coincide with the corners of a grid-cell.
			//therefore we need to add an offset and also ignore the outer strip that is pushed out because of the offset
			// if (point2d[0] === maxPoint[0] || point2d[1] === maxPoint[1]) continue;
			// const offset = cellSize / 2;
			// point2d = Vec2.add([offset, offset], point2d);
			renderHeightmapQuad(
				ctx, perspectiveCam, cellSize, scalarField, 
				point2d, color
			);
		}
	}
}


export type HeightMapSceneArgs = {
	canvas?: HTMLCanvasElement,
	//the gridField only provides information about the underlying grid, not the height-values! 
	gridField: GridField,
	//this is a continuous heightMap that is used to evaluate the heights of the grid-cells
	heightMap: ScalarField,
	shape: Shape
};

export type HeightMapScene = {
	updateHeightMap: (newHeightMap: ScalarField) => void
};


export function setupHeightMapScene(args: HeightMapSceneArgs): HeightMapScene {

	let canvas: HTMLCanvasElement = null;

	if (args.canvas){
		canvas = args.canvas;
	}
	else {
		document.body.insertAdjacentHTML("beforeend", "<canvas></canvas>");
		canvas = document.querySelector("canvas");
		autoAdjustCanvasSize(canvas, () => {
			updateCamera();
			render();
		});
		applyWindowSizeToCanvas(canvas);
	}

	let camera: OrthoOrbitCamera = {
		radius: 10,
		latitude: 0,
		longitude: 0,
		orthoSize: [canvas.width, canvas.height]
	};
	const updateCamera = () => {
		camera = {
			...camera,
			orthoSize: [canvas.width / 2, canvas.height / 2] as Vector2
		};
	};
	const transformCamera = (transformation: OrbitCamTransformation) => {
		camera = transformation(camera);
		render();
	};


	const gridField = args.gridField;
	let heightMap = args.heightMap;

	const ctx = canvas.getContext("2d");
	const backgroundColor = "#d4d3d2";

	//negative values are bluish, positive ones are reddish
	const negativeColor: Vector3 = [100, 173, 232];
	const positiveColor: Vector3 = [232, 83, 42];

	
	const render = () => {

		const { canvas } = ctx;
		const [w, h] = [canvas.width, canvas.height];

		ctx.save();
		ctx.fillStyle = backgroundColor;
		ctx.fillRect(0, 0, w, h);
		ctx.translate(w / 2, h / 2);
		ctx.scale(window.devicePixelRatio, -window.devicePixelRatio);

		const perspectiveCam = {
			...toRegularCamera(camera),
			settings: {
				planeWidthHalf: canvas.width,
				planeHeightHalf: canvas.height,
				zScale: 2000
			}
		} as PerspectiveCamera;
		
		Object.assign(ctx, {
			strokeStyle: "#3b3a39",
			lineWidth: 2,
			lineJoin: "round"
		} as Partial<CanvasRenderingContext2D>);

		renderHeightMap(ctx, perspectiveCam, negativeColor, positiveColor, args.shape, gridField, heightMap);

		ctx.restore();
	};

	const updateHeightMap = (newHeightMap: ScalarField) => {
		heightMap = newHeightMap;
		render();	
	};

	updateCamera();
	setupOrbitCameraControl(canvas, transformCamera);
	render();

	return { updateHeightMap };
}
